@import "./variables.scss";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $openSans;

    &::selection {
        color: white;
        background: $main;
    }
}

body {
    height: 100vh;
}

h1,
h2 {
    font-family: $ptSerif;
    font-weight: 700;
}

.container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100vh;

    .col {
        width: 100%;
        padding-top: 150px;

        display: flex;
        flex-direction: column;
        align-items: center;

        h3 {
            margin-bottom: 60px;
        }
    }
}

button.book {
    position: fixed;
    background: $inputText;
    color: white;
    border-radius: $borderRadius;
    border: none;
    padding: 10px 50px;
    font-size: 1.1rem;
    bottom: 60px;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    transition: background 0.25s ease;

    &:hover {
        background: $inputTextHover;
    }
}
